import { ChangeDetectionStrategy, Component, Input, OnInit, } from '@angular/core';
import { FormControl, FormGroup, ValidatorFn, Validators } from '@angular/forms';
//
import { TranslateService } from '@ngx-translate/core';
//
import { Brand } from '../../../ms-brands/models/brand';
import { Category } from '../../../ms-categories/models/category';
import { Collection } from '../../../ms-collections/models/collection';
import { Release } from '../../../ms-releases/models/releases';
import { COLORS, Color } from '../../../ms-releases/models/color';
import { GENDERS, Gender } from '../../../ms-releases/models/gender';
import { Shop } from '../../../ms-shops/models/shops';
import { Style } from '../../../ms-style/models/style';
import { Url } from '../../models/layout';
import { FilterItem } from '../../models/filters';

//
import { BaseReactiveFormComponent } from '../../../../../ui/components/base-reactive-form/base-reactive-form-component';

@Component({
    selector: 'filter-form',
    templateUrl: './filter-form.component.html',
    styleUrls: ['./filter-form.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})

export class FilterFormComponent extends BaseReactiveFormComponent<Array<FilterItem>> implements OnInit {

    genders: Gender[] = GENDERS;

    colors: Color[] = COLORS;

    filtersArray: Array<FilterItem> = [];

    @Input() filters: any;

    @Input() brands: Array<Brand>;

    @Input() categories: Array<Category>;

    @Input() collections: Array<Collection>;

    @Input() releases: Array<Release>;

    @Input() shops: Array<Shop>;

    @Input() styles: Array<Style>;

    constructor(translateService: TranslateService) {
        super(translateService);
        //setTranslations(this.translateService, TRANSLATIONS);
    }

    ngOnInit() {
        let validationsErrors: any[] = [
            {
                type: 'required',
                key: 'Required Field',
                params: null,
                translation: ''
            }
        ];

        this.validationErrorMessages = validationsErrors;

        this.createFormGroup();
    }

    createFormGroup() {

        this.formGroup = new FormGroup({
            style: new FormControl({ value: '', disabled: true }),
            sku: new FormControl({ value: '', disabled: true }),
            brand: new FormControl({ value: '', disabled: true }),
            collection: new FormControl({ value: '', disabled: true }),
            category: new FormControl({ value: '', disabled: true }),
            gender: new FormControl({ value: '', disabled: true }),
            color: new FormControl({ value: '', disabled: true }),
            shop: new FormControl({ value: '', disabled: true }),
            shipping: new FormControl({ value: '', disabled: true }),
            status: new FormControl({ value: '', disabled: true }),
            maxPrice: new FormControl({ value: '', disabled: true }),
            minPrice: new FormControl({ value: '', disabled: true }),
        });

    }

    submitClicked() {
        if (this.formGroup.valid) {
            this.accept.emit(this.filtersArray);
        } else {
            this.triggerValidation();
        }
    }

    addFilter(filter: string) {

        if (this.filtersArray.find(item => { return item.filter === filter })) {
            this.filtersArray = this.filtersArray.filter(item => {
                return item.filter !== filter;
            });
            this.formGroup.get(filter).setValue('');
            this.formGroup.get(filter).disable();
        } else {
            this.formGroup.get(filter).enable();

        }
    }

    updateFilterValue(filter: string, name: string) {
        if (!this.filtersArray.find(item => { return item.filter === filter })) {
            let filterItem = new FilterItem();
            filterItem = {
                filter: filter,
                name: name,
                value: this.formGroup.get(filter).value,
            }
            this.filtersArray = [...this.filtersArray, filterItem];
        }
        else {
            try {
                this.filtersArray.find(item => {
                    return item.filter === filter
                }).value = this.formGroup.get(filter).value;
                this.filtersArray.find(item => {
                    return item.filter === filter
                }).name = name;
            }
            catch (err) {
            }
        }

    }
}

